@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
*,*::before,*::after{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}body{font-family:"Segoe UI",Tahoma,Geneva,Verdana,sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.app{text-align:center;width:100%;max-width:2200px;background-color:#eee;height:100%;min-height:100vh;display:grid;grid-template-columns:1fr;grid-template-rows:auto 1fr auto}.route-container{margin:1em auto}@media screen and (min-width: 720px){.route-container{width:80%;margin:0 10%}}@media screen and (min-width: 1200px){.route-container{width:70%;margin:0 15%}}
.navbar-container{grid-row:1/2;background-color:#234475;display:grid;grid-template-columns:auto 1fr;align-items:center;padding:1em 1em;font-size:.75em}.navbar-menu{display:flex;justify-content:flex-end;align-items:center}.navbar-menu-item{margin-left:.5em}.navbar-menu-item:first-child{margin-left:0}.navbar-link{font-family:"Roboto";font-size:1.1em;font-weight:400;color:#f0f8ff;text-shadow:1px 1px 2px rgba(0,0,0,.6)}.navbar-link:active{color:#e28503}.site-art img{-webkit-animation:rotator 20s forwards infinite;animation:rotator 20s forwards infinite;-webkit-animation-delay:10s;animation-delay:10s;-webkit-animation-timing-function:linear;animation-timing-function:linear}@-webkit-keyframes rotator{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes rotator{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@media screen and (min-width: 520px){.navbar-container{font-size:.9em}}@media screen and (min-width: 720px){.navbar-container{font-size:1em}.navbar-menu-item{margin-left:1em}.navbar-menu-item:first-child{margin-left:0}}
.footer-container{grid-row:12/13;color:#f0f8ff;background-color:#234475;display:grid;grid-template-columns:1fr auto;padding:1em 1em;font-size:.7em}.footer-social{display:grid;grid-template-columns:repeat(12, 1fr)}.footer-social-container{display:flex;align-items:center;grid-column:span 3}.footer-copy{display:flex;align-items:center}.footer-link{color:#f0f8ff;padding-left:.2em;display:flex;align-items:center}.footer-link img{border-radius:50%;height:20px;margin-right:.2em}@media screen and (min-width: 500px){.footer-container{font-size:.9em}.footer-social-container{grid-column:span 3}}@media screen and (min-width: 720px){.footer-container{font-size:1em}.footer-social-container{grid-column:span 2}}@media screen and (min-width: 1020px){.footer-container{font-size:1.1em}.footer-social-container{grid-column:span 2}}@media screen and (min-width: 1200px){.footer-container{font-size:1em}.footer-social-container{grid-column:span 1}}

.formel-diller-title{margin-top:.5em;font-size:2em;color:#222;text-shadow:1px 1px 1px #fff}.index{font-size:1.7em;margin-bottom:.5em;text-decoration:underline}.formel-diller-content{display:flex;flex-direction:column;margin:1.5em 0}.table-of-contents{display:flex;flex-direction:column;margin-bottom:5em}.table-of-contents a{font-size:1.5em;color:#222}.table-of-contents a:visited,.table-of-contents a:active{color:#222}.table-of-contents a:hover{color:#e67e4e}.notes{width:100%;max-width:370px;align-self:center}.notes div{display:flex;flex-direction:column;margin-bottom:1em}.notes-paragraph{margin-bottom:1em;margin-left:5%;margin-right:5%;text-align:left}@media screen and (min-width: 480px){.notes{max-width:470px}.notes-paragraph{margin-left:0;margin-right:0}}@media screen and (min-width: 720px){.notes{max-width:700px}}@media screen and (min-width: 1000px){.notes{max-width:950px}}@media screen and (min-width: 1200px){.notes{max-width:1200px}}
.rectangle-info-container{background-color:orange;width:-webkit-min-content;width:-moz-min-content;width:min-content;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:1em 0}.rectangle-content{width:100%;overflow-y:scroll;text-align:center}.rectangle-content::-webkit-scrollbar{display:none}

